body {
    width: 100%;
    height: 100%;
}

p {
    display: inline-block;
    margin: 0px;
}

button {
    background: none;
    position: relative;

}

#todos {

    position: relative;
    margin-top: 10%;
    margin-bottom: 25%;
    width: 80%;
    height: 50%;
    margin-left: auto;
    margin-right: auto;
    padding: 30px;
    border: 1px solid #ddd;
    overflow: scroll;
}

@media (max-width:800px) {
    #todos {
        width: 80%;
    }

    #newText {
        max-width: 60%;
    }
}

.check {
    float: left;
    margin-right: 10px;
    width: 30px;
    height: 30px;
}

.check:hover {
    background-color: aqua;
}

.completed p {
    text-decoration: line-through;
    color: grey;
}

.check {
    background-color: blue;
}

.delete {
    float: right;
    font-size: 20px;
    padding: 5px 10px;
    background-color: red;
    color: white;
}

.delete:hover {
    background-color: black;
}

.todo {
    font-size: 30px;
    padding: 20px 0px;
    border-bottom: 3px dotted grey;
}

.todo:last-of-type {
    border-bottom: none;
}
